.search-form {
  height: 65px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: inset 0 0 0 40px #003cc5;
}

.body {
  background-color: #adb5bd;
}

.form-input {
  width: 250px;
  height: 30px;
  border-radius: 12px;
}

.btn-submit {
  min-height: 30px;
  cursor: pointer;
  background-color: #adb5bd;
  border: none;
  border-radius: 8px;
}

.form-control {
  width: 200px;
  height: 30px;
  border-radius: 15%;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 5px 10px;
  display: flex;
}

.gallery div {
  width: calc(25% - 10px);
  margin-bottom: 20px;
}

.gallery img {
  width: 100%;
  height: auto;
  border-radius: 5%;
}

.is-hidden {
  display: none;
}

.load-more {
  margin: 0 auto;
  margin: left;
  min-height: 30px;
  min-width: 120px;
  cursor: pointer;
  color: #fff;
  background: #0b63f6;
  border: none;
  border-radius: 10px;
  padding: 15px;
  transition: all .2s linear;
  display: block;
}

.load-more:hover {
  box-shadow: inset 0 0 0 40px #003cc5;
}

